import React, { Component } from 'react';
import shortid from 'shortid';
import styles from './SignUpForm.module.css';

export default class SignUpForm extends Component {
  state = {
    login: '',
    email: '',
    password: ''
  };

  loginInputId = shortid.generate();
  emailInputId = shortid.generate();
  passwordlInputId = shortid.generate();

  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

handleSubmit = e => {
  e.preventDefault();
  console.log('The link was clicked.');
this.props.onSignUp({...this.state});
this.reset();
};

reset = () => {
  this.setState({
    login: '',
      email: '',
      password: ''
  })
};

  render() {
    const { login, email, password } = this.state;

    return (
      <form className={styles.form} onSubmit={this.handleSubmit}>
        <label htmlFor={this.loginInputId}>
          Login
          <input
            type="text"
            value={login}
            name="login"
            onChange={this.handleChange}
            id={this.loginInputId}
          ></input>
        </label>
        <label htmlFor={this.emailInputId}>
          Email
          <input
            type="email"
            value={email}
            name="email"
            onChange={this.handleChange}
            id={this.emailInputId}
          ></input>
        </label>
        <label htmlFor={this.passwordlInputId}>
          Password
          <input
            type="password"
            value={password}
            name="password"
            onChange={this.handleChange}
            id={this.passwordlInputId}
          ></input>
        </label>
        <button type="submit" className={styles.button}>Sign up as {login}</button>
      </form>
    );
  }
}
